import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { VendorService } from '../view-vendor/vendor.service';
import { Vendor } from '../view-vendor/vendor';
import { VendorContact } from '../add-vendor/vendorContact';

@Component({
  selector: 'app-edit-vendor',
  templateUrl: './edit-vendor.component.html',
  styleUrls: ['./edit-vendor.component.scss']
})
export class EditVendorComponent implements OnInit {

  constructor(private route : ActivatedRoute, private vendorService : VendorService, private router : Router) { }

  vendor = new Vendor();
  vendors : Vendor[];

  vendorContact = new VendorContact();
  vendorContacts : VendorContact[];
  vendrId : any;
  ngOnInit() {
    this.vendrId = this.route.snapshot.paramMap.get('id');
    console.log(this.vendrId);
    this.getVendorContactById();
  }

  getVendorContactById(){
    this.vendorService.selectVendorContactById(this.vendrId).subscribe((data) =>{
      console.log(data);
      this.vendorContact = data;
    });

  }

  editVendor(){
    this.vendorService.editVendorS(this.vendorContact).subscribe((data) => {
      console.log(data);
      this.router.navigate(['/viewVendor']);
    });
  }

}
