import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Vendor } from './vendor';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';
import { VendorContact } from '../add-vendor/vendorContact';

@Injectable({
  providedIn: 'root'
})
export class VendorService {

  constructor(private httpService : HttpClient) { }

  getAllVendor() : Observable<Vendor[]>{
    return this.httpService.get<Vendor[]>(environment.APIUrl +'listAllVendor');
  }

  disableVendor(searchid:any) : any{
    let body = JSON.stringify(searchid);
    let headers = new HttpHeaders({'content-type':'application/json'});
    let options = {headers:headers};
    
    return this.httpService.put<Vendor>(environment.APIUrl +'deleteVendor/'+searchid,body,options);
  }

  addNewVendor(vendorContact : VendorContact) : any{
    let body = JSON.stringify(vendorContact);
    console.log(vendorContact);
    let headers = new HttpHeaders({'Content-Type': 'application/json'});
    let options = { headers: headers }
    
    return this.httpService.post(environment.APIUrl+'insertDetails', body, options);

  }

  selectVendorContactById(vendorId : any){
    return this.httpService.get<VendorContact>(environment.APIUrl + 'selectAllDetail/' + vendorId);
  }

  editVendorS(vendorContact : VendorContact) : any{
    let body = JSON.stringify(vendorContact);
    let headers = new HttpHeaders({'content-type':'application/json'});
    let options = {headers:headers};
    
    return this.httpService.put<Vendor>(environment.APIUrl +'editVendorContact',body,options);
  }


}
