import { Component, OnInit } from '@angular/core';
import { Vendor } from './vendor';

import { Router } from '@angular/router';
import { VendorService } from './vendor.service';

@Component({
  selector: 'app-view-vendor',
  templateUrl: './view-vendor.component.html',
  styleUrls: ['./view-vendor.component.scss']
})
export class ViewVendorComponent implements OnInit {

  constructor(private vendorService : VendorService, private router : Router) { }

  vendors : Vendor[];
  vendor = new Vendor();
  term;
  searchid : any;

  ngOnInit() {
    this.getVendors();
  }

  //get all vendors
  getVendors()
  {
    this.vendorService.getAllVendor()
    .subscribe((vendorData) =>{
      this.vendors = vendorData;
      console.log(vendorData);
    }, (error) =>{
      console.log(error);
    });
  }

  //navigate to edit vendo
  edit(vendorId : any){
    console.log(vendorId);
    this.router.navigate(['/editVendor',vendorId])
  }

    //navigate to login
  logOut(){
    this.router.navigate(['']);
  }

    //on select function
  onSelect(selectedVendor : any) {
    this.searchid = selectedVendor.vendorId;
}

//Disable function
disable(){
  //console.log("inside disable"+this.searchid);
  this.vendorService.disableVendor(this.searchid)
    .subscribe((data)=>{
      console.log("....."+data);
      this.router.navigate(['/viewVendor']);
    }, (error) =>{
      console.log(error);
    });
}

viewContact(venderId : any){
  console.log(venderId);
  this.router.navigate(['/viewContact', venderId]);
}

}
