import { Component, OnInit } from '@angular/core';
import { VendorService } from '../view-vendor/vendor.service';
import { VendorContact } from './vendorContact';
import { Router } from '@angular/router';

@Component({
  selector: 'app-add-vendor',
  templateUrl: './add-vendor.component.html',
  styleUrls: ['./add-vendor.component.scss']
})
export class AddVendorComponent implements OnInit {

  constructor(private vendorService : VendorService, private router : Router) { }
  vendorContacts : VendorContact[];
  vendorContact = new VendorContact();  

  ngOnInit() {
  }

  addVendor(){
    console.log(this.vendorContact);
    this.vendorService.addNewVendor(this.vendorContact).subscribe((data) => {
      console.log(data);
      this.router.navigate(['/viewVendor']);
    });

  }

}
